"use client"

import { useForm } from "react-hook-form"
import { zodResolver } from "@hookform/resolvers/zod"
import * as z from "zod"
import { submitRequest } from "../lib/api"
import { useState } from "react"
import { useAuth } from "../context/AuthContext"

const requestSchema = z.object({
  name: z.string().min(1, "Nama wajib diisi").min(2, "Nama minimal 2 karakter"),
  item: z.string().min(1, "Barang/Kendaraan harus dipilih"),
  purpose: z.string().min(1, "Keperluan wajib diisi").min(10, "Keperluan minimal 10 karakter"),
  date: z.string().min(1, "Tanggal harus diisi").refine((date) => {
    const selectedDate = new Date(date)
    const today = new Date()
    today.setHours(0, 0, 0, 0)
    return selectedDate >= today
  }, "Tanggal tidak boleh di masa lalu"),
})

type RequestFormValues = z.infer<typeof requestSchema>

const itemOptions = [
  "Mobil Dinas",
  "Motor Dinas", 
  "Laptop",
  "Proyektor",
  "Kamera",
  "Printer",
  "Alat Tulis Kantor",
  "Lainnya"
]

export default function RequestForm() {
  const { user } = useAuth()
  const { register, handleSubmit, formState: { errors }, reset, setValue } = useForm<RequestFormValues>({
    resolver: zodResolver(requestSchema),
    defaultValues: {
      name: user?.name || ""
    }
  })
  const [submitting, setSubmitting] = useState(false)
  const [message, setMessage] = useState<{ type: 'success' | 'error', text: string } | null>(null)

  // Set default name when user is available
  useState(() => {
    if (user?.name) {
      setValue("name", user.name)
    }
  })

  const onSubmit = async (data: RequestFormValues) => {
    setSubmitting(true)
    setMessage(null)
    try {
      await submitRequest(data)
      setMessage({ type: 'success', text: 'Permintaan berhasil dikirim! Menunggu persetujuan dari approver.' })
      reset({ name: user?.name || "" })
    } catch (error: any) {
      setMessage({ type: 'error', text: error.message || 'Terjadi kesalahan saat mengirim permintaan' })
    } finally {
      setSubmitting(false)
    }
  }

  return (
    <div className="max-w-2xl mx-auto">
      <form onSubmit={handleSubmit(onSubmit)} className="space-y-6">
        <div className="space-y-2">
          <label htmlFor="name" className="text-sm font-medium text-blue-800">
            Nama Lengkap *
          </label>
          <input 
            id="name"
            {...register("name")}
            placeholder="Masukkan nama lengkap Anda"
            className="flex h-10 w-full rounded-md border border-blue-300 bg-white px-3 py-2 text-sm text-blue-900 placeholder:text-blue-400 focus:outline-none focus:ring-2 focus:ring-blue-500 focus:border-transparent disabled:cursor-not-allowed disabled:opacity-50"
          />
          {errors.name && <p className="text-sm text-red-600 font-medium">{errors.name.message}</p>}
        </div>

        <div className="space-y-2">
          <label htmlFor="item" className="text-sm font-medium text-blue-800">
            Barang/Kendaraan *
          </label>
          <select 
            id="item"
            {...register("item")}
            className="flex h-10 w-full rounded-md border border-blue-300 bg-white px-3 py-2 text-sm text-blue-900 focus:outline-none focus:ring-2 focus:ring-blue-500 focus:border-transparent disabled:cursor-not-allowed disabled:opacity-50"
          >
            <option value="">Pilih barang/kendaraan</option>
            {itemOptions.map((option) => (
              <option key={option} value={option}>{option}</option>
            ))}
          </select>
          {errors.item && <p className="text-sm text-red-600 font-medium">{errors.item.message}</p>}
        </div>

        <div className="space-y-2">
          <label htmlFor="purpose" className="text-sm font-medium text-blue-800">
            Keperluan Dinas *
          </label>
          <textarea 
            id="purpose"
            {...register("purpose")}
            placeholder="Jelaskan keperluan dinas Anda secara detail..."
            rows={4}
            className="flex min-h-[80px] w-full rounded-md border border-blue-300 bg-white px-3 py-2 text-sm text-blue-900 placeholder:text-blue-400 focus:outline-none focus:ring-2 focus:ring-blue-500 focus:border-transparent disabled:cursor-not-allowed disabled:opacity-50"
          />
          {errors.purpose && <p className="text-sm text-red-600 font-medium">{errors.purpose.message}</p>}
        </div>

        <div className="space-y-2">
          <label htmlFor="date" className="text-sm font-medium text-blue-800">
            Tanggal Peminjaman *
          </label>
          <input 
            id="date"
            type="date"
            {...register("date")}
            min={new Date().toISOString().split('T')[0]}
            className="flex h-10 w-full rounded-md border border-blue-300 bg-white px-3 py-2 text-sm text-blue-900 focus:outline-none focus:ring-2 focus:ring-blue-500 focus:border-transparent disabled:cursor-not-allowed disabled:opacity-50"
          />
          {errors.date && <p className="text-sm text-red-600 font-medium">{errors.date.message}</p>}
        </div>

        <button 
          type="submit"
          disabled={submitting}
          className="w-full inline-flex items-center justify-center rounded-md text-sm font-medium transition-colors focus-visible:outline-none focus-visible:ring-2 focus-visible:ring-yellow-400 focus-visible:ring-offset-2 disabled:pointer-events-none disabled:opacity-50 bg-gradient-to-r from-blue-600 to-blue-700 text-white hover:from-blue-700 hover:to-blue-800 h-11 px-8 shadow-lg"
        >
          {submitting ? "Mengirim Permintaan..." : "Kirim Permintaan"}
        </button>

        {message && (
          <div className={`p-4 rounded-lg border ${
            message.type === 'success' 
              ? 'bg-green-50 text-green-800 border-green-200' 
              : 'bg-red-50 text-red-800 border-red-200'
          }`}>
            <p className="text-sm font-medium">{message.text}</p>
          </div>
        )}
      </form>
    </div>
  )
}
