"use client"

import RequestForm from "../../components/RequestForm"
import { useEffect } from "react"
import { useAuth } from "../../context/AuthContext"
import { useRouter } from "next/navigation"

export default function PeminjamPage() {
  const { isAuthenticated, role, user } = useAuth()
  const router = useRouter()

  useEffect(() => {
    if (!isAuthenticated) {
      router.push("/")
      return
    }
    
    if (role !== "peminjam") {
      // Jika user bukan peminjam, redirect ke halaman yang sesuai
      if (role === "approver") {
        router.push("/approver")
      } else {
        router.push("/")
      }
    }
  }, [isAuthenticated, role, router])

  if (!isAuthenticated || role !== "peminjam") {
    return (
      <div className="flex items-center justify-center min-h-[60vh]">
        <div className="text-center">
          <div className="w-8 h-8 border-4 border-blue-600 border-t-transparent rounded-full animate-spin mx-auto mb-4"></div>
          <p className="text-blue-600">Memverifikasi akses...</p>
        </div>
      </div>
    )
  }

  return (
    <div className="space-y-8 bg-gradient-to-br from-blue-50 to-yellow-50 min-h-screen -mx-4 -my-8 px-4 py-8">
      <div className="text-center space-y-4">
        <h1 className="text-3xl font-bold tracking-tight text-blue-900">Dashboard Peminjam</h1>
        <div className="bg-white rounded-lg p-4 shadow-sm border border-blue-200 max-w-2xl mx-auto">
          <p className="text-blue-700">
            Selamat datang, <span className="font-semibold text-blue-900">{user?.name}</span>! 
            Ajukan permintaan peminjaman barang atau kendaraan untuk keperluan dinas Anda.
          </p>
        </div>
      </div>

      <div className="bg-white rounded-xl shadow-lg p-6 border border-blue-200">
        <h2 className="text-xl font-semibold mb-4 text-blue-900">Form Permintaan Peminjaman</h2>
        <div className="grid grid-cols-1 md:grid-cols-3 gap-4 mb-6 text-sm">
          <div className="flex items-center space-x-2 bg-blue-50 p-3 rounded-lg">
            <div className="w-3 h-3 bg-blue-500 rounded-full"></div>
            <span className="text-blue-700 font-medium">Isi form dengan lengkap</span>
          </div>
          <div className="flex items-center space-x-2 bg-yellow-50 p-3 rounded-lg">
            <div className="w-3 h-3 bg-yellow-500 rounded-full"></div>
            <span className="text-yellow-700 font-medium">Menunggu persetujuan</span>
          </div>
          <div className="flex items-center space-x-2 bg-green-50 p-3 rounded-lg">
            <div className="w-3 h-3 bg-green-500 rounded-full"></div>
            <span className="text-green-700 font-medium">Permintaan disetujui</span>
          </div>
        </div>
        <RequestForm />
      </div>

      <div className="bg-white rounded-xl shadow-lg p-6 border border-blue-200">
        <h3 className="text-lg font-semibold mb-4 text-blue-900">Informasi Penting</h3>
        <div className="grid grid-cols-1 md:grid-cols-2 gap-6">
          <div className="space-y-3 text-sm">
            <h4 className="font-medium text-blue-800">Prosedur Peminjaman:</h4>
            <div className="space-y-2">
              <div className="flex items-start space-x-2">
                <div className="w-2 h-2 bg-blue-500 rounded-full mt-2 flex-shrink-0"></div>
                <p className="text-blue-700">Permintaan akan diproses dalam 1-2 hari kerja setelah disubmit</p>
              </div>
              <div className="flex items-start space-x-2">
                <div className="w-2 h-2 bg-blue-500 rounded-full mt-2 flex-shrink-0"></div>
                <p className="text-blue-700">Pastikan tanggal peminjaman tidak bentrok dengan jadwal lain</p>
              </div>
              <div className="flex items-start space-x-2">
                <div className="w-2 h-2 bg-blue-500 rounded-full mt-2 flex-shrink-0"></div>
                <p className="text-blue-700">Hubungi admin jika ada pertanyaan terkait status permintaan</p>
              </div>
            </div>
          </div>
          <div className="space-y-3 text-sm">
            <h4 className="font-medium text-blue-800">Ketentuan Umum:</h4>
            <div className="space-y-2">
              <div className="flex items-start space-x-2">
                <div className="w-2 h-2 bg-yellow-500 rounded-full mt-2 flex-shrink-0"></div>
                <p className="text-blue-700">Barang/kendaraan harus dikembalikan sesuai jadwal yang telah ditentukan</p>
              </div>
              <div className="flex items-start space-x-2">
                <div className="w-2 h-2 bg-yellow-500 rounded-full mt-2 flex-shrink-0"></div>
                <p className="text-blue-700">Peminjam bertanggung jawab atas kerusakan atau kehilangan</p>
              </div>
              <div className="flex items-start space-x-2">
                <div className="w-2 h-2 bg-yellow-500 rounded-full mt-2 flex-shrink-0"></div>
                <p className="text-blue-700">Gunakan barang/kendaraan sesuai dengan keperluan dinas yang diajukan</p>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  )
}
